body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #efefef;
}

.board {
  position: relative;
  background-color: white;
  border-radius: 15px;
  box-shadow: 0px 1px 6px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  width: 300px;
  height: 300px;
  margin: 30px auto;
}

.game-over {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: white;
  opacity: .6;
  border-radius: 15px;
}

.game-over p {
  padding-top: 30px;
  text-align: center;
  font-weight: lighter;
  font-size: 1.2em;
  line-height: 1.7em;
}
